@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@400;600;700&display=swap);
.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5vh;
}

.bottom img {
  height: 10vh;
}
.content.center {
  display: flex;
  justify-content: unset;
  padding: 0;
  padding-left: 2vw;
}

.app {
  height: 100%;
  width: 100%;
  display: flex;
  padding: 1vh 1vh 0 1vh;
}

.info {
  width: 50vw;
  margin-right: 5vw;
}

.info a{
  color: #81ff6d;
}

.info strong {
  color: #81ff6d;
  font-weight: 700;
}

.info input,select {
  height: 2.5rem;
  background-color: transparent;
  border: 1px solid #81ff6d;
  border-top: none;
  border-left: none;
  border-right: none;
  padding-left: 5px;
  color: #ddd;
  font-size: 1.2rem;
  margin: 0 10px;
}

.info select option {
  background-color: transparent;
  color: #000;
}

.info form {
  display: flex;
  margin-top: 5vh;
  align-items: center;
}

.info .button {
  height: 5vh;
  width: 5vw;
  line-height: 4vh;
}

.permutations {
  height: 80%;
  width:  auto;
  border-left: 1px solid #80ff6d52;
  border-right: 1px solid #80ff6d52;
  padding: 2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.permutations #perm-count {
  margin: 0;
}

.scroll {
  height: 90%;
  overflow: scroll;

}

.scroll::-webkit-scrollbar {
  display: none;
}

.scroll ul li {
  list-style: none;
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-weight: 700;
  color: #81ff6d;
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(#444444, #111111);
  overflow: hidden;
}

h1,h3 {
  font-family: 'Bebas Neue', cursive;
  text-transform: uppercase;
}

p,a,select,input,li,i {
  font-family: Cairo, sans-serif;
}

a{
  color: #81ff6d;
}

strong {
  color: #81ff6d;
  font-weight: 700;
}


.content {
  height: 90vh;
  width: 90vw;
  padding: 0 5vw;
  background-color: transparent;
  border-left: 1px solid #81ff6d;
  display: flex; 
  justify-content: center;
  flex-direction: column;
}

.content h1 {
  font-size: 3rem;
  font-weight: 500;
  color: #ddd;
  margin-bottom: 1.5rem;
}

.content h3 {
  font-size: 1.5rem;
  font-weight: 400;
  color: #ddd;
  margin: 1.5rem 0;
}

.content p {
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: #aaa;
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.button {
  height: 8vh;
  width: 10vw;
  background-color: transparent;
  border: 2px solid #81ff6d;
  border-radius: 8vh;
  text-decoration: none;
  text-align: center;
  line-height: 7vh;
  color: #81ff6d;
  transition: all 300ms;
  font-size: 1.2rem;
  font-weight: 600;
}

.button:hover {
  background-color: #81ff6d;
  color: #111;
}

